import { Injectable } from '@angular/core';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material';
import { MatConfirmDialogComponent } from './mat-confirm-dialog/mat-confirm-dialog.component';

@Injectable({
  providedIn: 'root'
})
export class DialogService {
  openConfirmDialog() {
    throw new Error('Method not implemented.');
  }

  constructor(private dialog:MatDialog) { }

  OpenConfirmDialog(msg){
    return this.dialog.open(MatConfirmDialogComponent,{//(open method )
       width:'390px',//it return modelref
       panelClass:'confirm-dialog-container', //custom class (css class)
     //Panel class is used to allow our customize dialog box to apply css & make it as custom
       disableClose:true,//for dialog box
       position:{top:"35vh"},//position 

       data:{
        message:msg
      }
      })
    }
}
