// import { Observable } from 'rxjs';
import { DialogService } from './../shared/dialog.service';
import { Title } from '@angular/platform-browser';
import { HttpService } from './../http.service';
import { Component, OnInit, TemplateRef } from '@angular/core';
import { BsModalService,BsModalRef } from 'ngx-bootstrap/modal';
import { Employee } from '../model/employee';
import { MAT_DIALOG_DATA } from '@angular/material';
import { ToastrService  } from 'ngx-toastr';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  empobj=<Employee>{}
  Empdata:any[]=[];
  // Empdata:Observable<Employee[]>;
  p:number=1;
  nameSearch:string='';


  isRadioSelect:boolean=false;
  modalRef: BsModalRef;  
   
  config = {
    animated: true,  //smoot animation  
    ignoreBackdropClick: true, //open pop up all time /no need to destroy
    class: "alert alert-danger"
  };

 constructor(private service:HttpService,
    private title:Title,
    private modalservice:BsModalService,
    private dialogService:DialogService,
    private toaster:ToastrService) { 

    }
ngOnInit() {
    this.getEmpData();
    this.title.setTitle('Admin|Dashboard')

 }
   getEmpData(){
   this.service.GetAllEmployeeData()
   .subscribe((response)=>{
    // console.log(response)
    this.Empdata=(<any>response)
  })
  //  this.Empdata=this.service.GetAllEmployeeData();
}


onEdit(item){
  // console.log(item)
  this.isRadioSelect=true;
  this.empobj=item;
}
IsRadioCheck(){
  if(this.isRadioSelect)
  return true;
  else
  return false
}
onUpdate(popup: TemplateRef<any>){
  if(this.IsRadioCheck()){
    //update a record
     this.modalRef = this.modalservice.show(popup, this.config);
}else{
    // alert("Please Select a record to Update !!!")
      this.toaster.warning("Please Select a record to Update!!!")
    // this.toaster.error("Please Select a record to Update!!!")
  }
}
onDelete(){
  if(this.IsRadioCheck()){

    //delete a record
    this.dialogService.OpenConfirmDialog('Are you sure to delete this record?')
    .afterClosed()
    .subscribe((res)=>{
      console.log(res);
      if(res){
        ///Delete the record..
        this.service.DeleteRecord(this.empobj.id)
        .subscribe((response)=>{
          // console.log(response)
          this.toaster.success(response,"Record Deleted Sucessfully");
          this.getEmpData();
        })
      }else{
        //Don't delete the record
        }})
   }else{
     alert("Please Select a record to Delete !!!");

  }

 
}
onChangeStatus(){
    
}

 }


