import { Router } from '@angular/router';
import { HttpService } from './../http.service';
import { NgForm } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';

@Component({
  selector: 'app-add-employee',
  templateUrl: './add-employee.component.html',
  styleUrls: ['./add-employee.component.css']
})
export class AddEmployeeComponent implements OnInit {
  allCountry:any[]=[]
  constructor(private service: HttpService,
                  private router:Router,
                  private title: Title){ }
                  
  ngOnInit() {
  this.GetAllCountry();
  this.title.setTitle("Admin|Add Employee")
  }
   GetAllCountry(){
   this.service.getAllCountry()
    .subscribe((response)=>{
      this.allCountry=(<any>response)
    })
 }
  onSubmit(f:NgForm){
  
    let obj={
      name:f.value.name,
      mobileNo:f.value.mobileNo,
      department:f.value.department,
      status:f.value.status,
      country:f.value.country,
      createdBy:sessionStorage.getItem("username"),
   
      //  createdDate:"17/08/2022",
       createdDate:new Date(),
      // createdDate :new Date().toLocaleString().slice(0,9),
       updatedBy:sessionStorage.getItem("username"),
       updatedDate:new Date()
      //updatedDate:new Date().toLocaleString().slice(0,9)
      // updatedDate:"17/08/2022"
     }

     this.service.addEmployee(obj)
     .subscribe((response)=>{
      // console.log(response);
      this.router.navigate(['/home'])
       })

  }

}




